@import "vocaloid-colors";
:root {/**these colors are (so far) only used on the song pages so putting them with the rest of the vocaloid colors felt like overkill**/
	--rinlen-color: #EBCA2C;
	--vy1-color: #97F4E6;
}

#intro {/**man idk what to say. styles the intro.**/
	margin: 0 auto 10px auto;
	min-height: unset;
	padding: 10px;
	max-width: 720px;
	flex-direction: column;
	gap:0;
	p {
		text-align: left;
		padding: .25em;
	}
	.introImage {/**uncircles your image**/
		border-radius: 15px;
		min-height: unset;
		min-width: unset;
		max-height: 200px;
		max-width: 100%;
	}
}

section {
	padding:0;
	margin-bottom:10px;
}

.albums {
	align-content: flex-start;
	align-items: flex-start;
	justify-content: space-around;
}

.subtitle {/**expand subtitle. also this subtitle is the one and only reason the intro box is a flex box**/
	border-radius: .5em;
	padding: .1em .4em;
	font-size: larger;
	margin: .5em auto;
	max-width: 75%;
}
/**and then these are all just subtitle colorings**/
.subtitle.miku {
	padding: .1em;
	box-shadow: 0 0 0.25em 0.25em var(--miku-color);
	background-color: var(--miku-color);
