/**add some padding between the quote and the singer circles**/
.singers {
	padding-top: 5px;
}
/**styles the singer circles**/
.singers span {
	width: 20px;
	height: 20px;
	display: inline-block;
	color: transparent;
	overflow: hidden;
	border-radius: 50%;
	font-size: 10px;
	line-height: 20px;
	margin: 0 2.5px;
	cursor: default;
}

/**add colors depending on class**/
.singers span.miku {
	background-color: var(--miku-color);
}
.singers span.rin {
	background-color: var(--rin-color);
}
.singers span.len {
	background-color: var(--len-color);
