}
.singers span.vy1 {
	background-color: var(--vy1-color);
}
.singers span.human {
	background-color:black;
}

/**hi welcome to hell this is when the singer is secondary and the border is colored instead of the whole circle**/
.singers span.secondary {
	border: 2px solid;
	background-color: transparent !important;
}

.singers span.rin.secondary {
	border-color: var(--rin-color);
}
.singers span.len.secondary {
	border-color: var(--len-color);
}
.singers span.luka.secondary {
	border-color: var(--luka-color);
}
.singers span.kaito.secondary {
